// Import Styled
import styled from 'styled-components';

export const MenuContent = styled.div`
	padding-top: 32px;
	a{
		text-decoration: none;
		color: #fff;    
		border-radius: 16px;
	    background-color: transparent;
	    color: #fff;
	    font-weight: normal;
	    font-family: "Poppins";
	    font-size: 16px;
	    line-height: 21px;
	    letter-spacing: 0.75px;
	    margin-right: 24px;
	    padding: 4px 12px 7px;
	    &:hover{
	    	font-weight: 600;
	    	font-family: "Roboto";
	    	background-color: #7481D1;
	    }
	}
`;